import React, { useState, useEffect } from "react";
import "./../assets/style.scss";
import { makeStyles } from "@material-ui/core/styles";
import {
  Drawer,
  CssBaseline,
  AppBar,
  Toolbar,
  List,
  Divider,
  ListItem,
  ListItemIcon,
  ListItemText,
  Link as MaterialLink,
} from "@material-ui/core";
import Info from "@material-ui/icons/InfoRounded";
import Home from "@material-ui/icons/HomeRounded";
import MenuBook from "@material-ui/icons/MenuBookRounded";
import Book from "@material-ui/icons/BookRounded";
import ThumbUp from "@material-ui/icons/ThumbUpRounded";
import ContactMail from "@material-ui/icons/ContactMailRounded";
import { Link, Route, Switch } from "react-router-dom";
import LearningPage from "./LearningPage";
import About from "./About";
import Blog from "./Blog";
import ContactUs from "./ContactUs";
import Donate from "./Donate";
import HomePage from "./HomePage";
import Telegram from "@material-ui/icons/Telegram";
import Twitter from "@material-ui/icons/Twitter";
import Instagram from "@material-ui/icons/Instagram";
import WhatsApp from "@material-ui/icons/WhatsApp";
import SendCourse from "./other/SendCourse";

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  appBar: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginRight: drawerWidth,
    backgroundColor: "white",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    background:
      "linear-gradient(0deg, rgba(13,71,161 ,1) 50%, rgba(30,136,229 ,1) 100%)",
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.default,
    padding: theme.spacing(3),
  },
  textRight: {
    textAlign: "right",
    direction: "rtl",
    color: "white",
  },
  Vazir: {
    fontFamily: "Vazir",
  },
  active: {
    background: "rgba(0,0,0 ,0.2)",
    transition: ".8s",
    "&:hover": {
      background: "rgba(0,0,0 ,0.2)",
      transition: ".8s",
    },
  },
  icons: {
    color: "rgba(69,90,100 ,1)",
    fontSize: "33px",
    transition: ".6s",
  },
  toolbarCenter: {
    textAlign: "left",
    display: "inline-block",
  },
  iconLink: {
    margin: "14px 18px",
    display: "inline-block",
  },
  teleHover: {
    "&:hover": {
      color: "rgba(21,101,192 ,1)",
      transition: ".6s",
    },
  },
  twHover: {
    "&:hover": {
      color: "rgba(33,150,243 ,1)",
      transition: ".6s",
    },
  },
  instaHover: {
    "&:hover": {
      color: "rgba(255,23,68 ,1)",
      transition: ".6s",
    },
  },
  whHover: {
    "&:hover": {
      color: "rgba(0,150,136 ,1)",
      transition: ".6s",
    },
  },
  colorBlack: {
    color: "black",
    textAlign: "right",
    direction: "rtl",
    float: "right",
    marginTop: "14px",
    fontSize: "25px",
  },
  colorWhite:{
    color: "#FAFAFA"
  }
}));

export default function App() {
  const [active, setActive] = useState(window.location.pathname);
  const classes = useStyles();
  let div = "<DIV>MR</DIV>";
  let title = "";
  useEffect(() => {
    window.onpopstate  = () => {
      setActive(window.location.pathname)
    }
  }, [active])
  let bgOn = false;
  if (active === "/") {
    title = "صفحه‌ی اصلی";
    bgOn = true;
  } else if (active === "/learning") {
    title = "آموزش‌ها";
    bgOn = false;
  } else if (active === "/donate") {
    title = "حمایت";
    bgOn = false;
  } else if (active === "/blog") {
    title = "بلاگ";
    bgOn = false;
  } else if (active === "/about") {
    title = "درباه‌ی ما";
    bgOn = false;
  } else if (active === "/contact-us") {
    title = "تماس با ما";
    bgOn = false;
  } else if (active === "/send-course") {
    title = "پیشنهاد دوره";
    bgOn = false;
  }
  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar className={classes.toolbarCenter}>
          <MaterialLink className={classes.iconLink} href="https://t.me">
            <Telegram className={classes.icons + " " + classes.teleHover} />
          </MaterialLink>
          <MaterialLink className={classes.iconLink} href="https://t.me">
            <Twitter className={classes.icons + " " + classes.twHover} />
          </MaterialLink>
          <MaterialLink className={classes.iconLink} href="https://t.me">
            <Instagram className={classes.icons + " " + classes.instaHover} />
          </MaterialLink>
          <MaterialLink className={classes.iconLink} href="https://t.me">
            <WhatsApp className={classes.icons + " " + classes.whHover} />
          </MaterialLink>
          <h2 className={classes.colorBlack}>{title}</h2>
        </Toolbar>
      </AppBar>
      <main className={classes.content + (bgOn ? " backgroundImg" : "")}>
        <div className={classes.toolbar} />
        <Switch>
          <Route path="/" exact>
            <HomePage />
          </Route>
          <Route path="/learning">
            <LearningPage />
          </Route>
          <Route path="/blog">
            <Blog />
          </Route>
          <Route path="/donate">
            <Donate />
          </Route>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/contact-us">
            <ContactUs />
          </Route>
          <Route path="/send-course">
            <SendCourse />
          </Route>
        </Switch>
      </main>
      <Drawer
        className={classes.drawer}
        variant="permanent"
        classes={{
          paper: classes.drawerPaper,
        }}
        anchor="right"
      >
        <div className={classes.toolbar}>
        <a className={classes.colorWhite} href="https://mrdiv.dev"><h1 className="header-logo">{div}</h1></a>
        </div>
        <Divider />
        <List>
          <Link to="/">
            <ListItem
              onClick={() => setActive("/")}
              className={active === "/" ? classes.active : ""}
              button
              key={"خانه"}
            >
              <ListItemText
                className={classes.textRight + " textBold"}
                primary={"خانه"}
              />
              <ListItemIcon className={classes.textRight}>
                {<Home />}
              </ListItemIcon>
            </ListItem>
          </Link>
          <Link to="/learning">
            <ListItem
              onClick={() => setActive("/learning")}
              className={active === "/learning" ? classes.active : ""}
              button
              key={"آموزش"}
            >
              <ListItemText
                className={classes.textRight + " textBold"}
                primary={"آموزش"}
              />
              <ListItemIcon className={classes.textRight}>
                {<MenuBook />}
              </ListItemIcon>
            </ListItem>
          </Link>
          <Link to="/blog">
            <ListItem
              onClick={() => setActive("/blog")}
              className={active === "/blog" ? classes.active : ""}
              button
              key={"وبلاگ"}
            >
              <ListItemText
                className={classes.textRight + " textBold"}
                primary={"بلاگ"}
              />
              <ListItemIcon className={classes.textRight}>
                {<Book />}
              </ListItemIcon>
            </ListItem>
          </Link>
          <Link to="/donate">
            <ListItem
              onClick={() => setActive("/donate")}
              className={active === "/donate" ? classes.active : ""}
              button
              key={"حمایت"}
            >
              <ListItemText
                className={classes.textRight + " textBold"}
                primary={"حمایت"}
              />
              <ListItemIcon className={classes.textRight}>
                {<ThumbUp />}
              </ListItemIcon>
            </ListItem>
          </Link>
          <Link to="/contact-us">
            <ListItem
              onClick={() => setActive("/contact-us")}
              className={active === "/contact-us" ? classes.active : ""}
              button
              key={"تماس با ما"}
            >
              <ListItemText
                className={classes.textRight + " textBold"}
                primary={"تماس با ما"}
              />
              <ListItemIcon className={classes.textRight}>
                {<ContactMail />}
              </ListItemIcon>
            </ListItem>
          </Link>
          <Link to="/about">
            <ListItem
              onClick={() => setActive("/about")}
              className={active === "/about" ? classes.active : ""}
              button
              key={"درباره‌ی ما"}
            >
              <ListItemText
                className={classes.textRight + " textBold"}
                primary={"درباره‌ی ما"}
              />
              <ListItemIcon className={classes.textRight}>
                {<Info />}
              </ListItemIcon>
            </ListItem>
          </Link>
        </List>
      </Drawer>
    </div>
  );
}
